/* Анимация "прожектора" */
@keyframes reveal {
  from {
    /* Начинаем с маленького круга в центре */
    clip-path: circle(0% at 50% 50%);
  }

  to {
    /* Заканчиваем кругом, который полностью покрывает экран (150% чтобы наверняка) */
    clip-path: circle(150% at 50% 50%);
  }
}

.hero {
  position: relative;
  color: white;
  padding: 60px 20px 100px 20px;
  text-align: center;
  min-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  /* Применяем анимацию "прожектора". Она длится 2 секунды */
  animation: reveal 2s ease-out forwards;
}

/* Псевдоэлемент для фонового изображения с прозрачностью */
.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: var(--hero-bg-image);
  background-size: cover;
  background-position: center;
  /* Делаем изображение полупрозрачным, чтобы частицы были видны */
  /* Можно настроить от 0.2 до 0.5 в зависимости от желаемой видимости */
  opacity: 0.3;
  z-index: 0;
}

/* Overlay для затемнения и читаемости текста */
.hero::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* Легкий градиент для лучшей видимости частиц и читаемости текста */
  /* Уменьшили непрозрачность, чтобы частицы были более заметны */
  background: linear-gradient(
    to bottom,
    rgba(15, 23, 42, 0.1) 0%,
    rgba(15, 23, 42, 0.25) 100%
  );
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  /* max-width уехал в .container */
  /* Небольшая задержка для появления текста, чтобы сначала начал работать "прожектор" */
  opacity: 0;
  animation: fadeIn 1s ease-out 0.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.cta-button {
  background-color: #38bdf8;
  color: #0f172a;
  border: none;
  padding: 15px 30px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s;
  text-decoration: none;
}

.cta-button:hover {
  background-color: #0ea5e9;
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(56, 189, 248, 0.5);
}

.cta-button.secondary {
  background-color: transparent;
  border: 1px solid #38bdf8;
  color: #38bdf8;
}

.cta-button.secondary:hover {
  background-color: rgba(56, 189, 248, 0.1);
  color: #ffffff;
}

/* Адаптивность */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2.2rem;
  }

  .hero p {
    font-size: 1rem;
  }
}